:is(.flex-col, .flex) {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.grid {
  display: grid;
}

.uno-rem {
  gap: 1em;
}

body {
  background-color: var(--Dark-moderate-cyan);
  overflow-x: hidden;
}

:is(header, main, section, footer) {
  width: min(90rem, 100% - 0.5rem);
  margin-inline: auto;
}

.header {
  display: none;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: min(5vh, 1rem) 0.5rem;
  height: 50px;
}
.header__logo {
  flex-basis: 9.375em;
  flex-grow: 1;
  align-items: center;
  gap: 0.375em;
}
.header__logo--image {
  width: 3.125em;
  border-radius: 50%;
}
.header__logo--letters {
  flex: 2;
  font-weight: 600;
}
.header__nav {
  flex-direction: column;
  justify-content: space-around;
  gap: 1rem;
  position: absolute;
  inset: 50px 0 0 30%;
  padding: min(20vh, 10rem) 2rem;
  background-color: hsla(0, 20%, 100%, 0.9);
  backdrop-filter: blur(1em);
  transition: all 0.5s ease-in-out;
  transform: translateX(120%);
}
@media (min-width: 40em) {
  .header__nav {
    position: relative;
    inset: 0;
    height: auto;
    flex-direction: row;
    justify-content: flex-end;
    flex: 1;
    display: flex;
    aspect-ratio: 0;
    background-color: transparent;
    backdrop-filter: blur(0);
    transform: translateX(0);
  }
}
.header__links {
  font-size: 1.375rem;
  color: var(--Very-dark-desaturated-blue);
}
@media (min-width: 40em) {
  .header__links {
    color: white;
  }
}
.header__toggle {
  width: 2rem;
  aspect-ratio: 1;
  background-image: url(../../../assets/icons/icon-hamburger.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  background-size: contain;
  border: 1.5px solid transparent;
  cursor: pointer;
}
@media (min-width: 40em) {
  .header__toggle {
    display: none;
  }
}

.main {
  grid-template-areas: "one";
  place-items: center;
  align-items: center;
  background: var(--Soft-red);
}
.main__video {
  grid-area: one;
  object-fit: cover;
  aspect-ratio: 9/16;
}
@media (min-width: 40em) {
  .main__video {
    aspect-ratio: 16/9;
  }
}
.main__h1 {
  grid-area: one;
  color: var(--White);
  font-size: clamp(1.75rem, 1rem + 5vw, 3rem);
  z-index: 2;
}

.about {
  --container-width: 400;
  background-color: var(--White);
  grid-template-rows: 1fr;
}
.about__services {
  flex-wrap: wrap;
}
.about__text {
  flex-basis: 25rem;
  flex-grow: 1;
  gap: 1em;
  justify-content: center;
  padding: 1.875em;
}
.about__image {
  flex-basis: 25rem;
  flex-grow: 1;
  width: 500px;
  aspect-ratio: 4/4;
  object-fit: cover;
}
.about__h2 {
  font-size: clamp(1.25rem, 1rem + 2.5vw, 2.25rem);
}
.about__p {
  color: var(--Dark-desaturated-cyan);
}
.about__link {
  color: var(--Very-dark-desaturated-blue);
  font-size: 1.25rem;
  font-weight: 800;
  width: fit-content;
  position: relative;
}
.about__link::after {
  content: "";
  position: absolute;
  inset: 20px 0 0 0;
  height: 5px;
  width: auto;
  opacity: 0.8;
  background-color: var(--Soft-red);
}
.about__music {
  flex-wrap: wrap;
}
.about__info {
  flex-basis: 25rem;
  flex-grow: 1;
  grid-template-areas: "video";
  place-items: center;
  position: relative;
  overflow: visible;
}
.about__text--video {
  grid-area: video;
  text-align: center;
  z-index: 2;
}
.about__h3--video {
  color: var(--White);
  font-size: 2rem;
}
.about__p--video {
  color: var(--White);
  font-weight: 600;
  font-size: 1.375rem;
  margin: 1em 0;
}
.about__btn {
  color: var(--White);
  padding: 1em 0.5em;
  border: 2px solid var(--White);
  border-radius: 16px;
  font-size: 1.375rem;
  z-index: 99;
}
.about__video {
  grid-area: video;
  aspect-ratio: 3/3;
  object-fit: cover;
}

.testimonials {
  background-color: var(--Soft-red);
  padding: 1.25em;
  justify-content: center;
  gap: 2em;
}
.testimonials__h2 {
  text-align: center;
  color: var(--White);
  font-size: 2.375rem;
}
.testimonials__container {
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(min(22rem, 100%), 1fr));
  align-items: start;
}
.testimonials__reviews {
  background-color: var(--White);
  padding: 1em;
  gap: 1.75em;
  height: fit-content;
  border-radius: 16px;
  box-shadow: 0 0 16px rgba(95, 95, 95, 0.794);
}
.testimonials__names {
  gap: 0.5em;
  align-items: center;
  text-align: center;
  width: 100%;
}
.testimonials__name {
  font-weight: 600;
  font-size: 1.125rem;
}
.testimonials__position {
  color: var(--Dark-moderate-cyan);
  font-weight: 600;
}
.testimonials__image {
  width: 1.875em;
  border-radius: 50%;
}
.gallery {
  padding: 1.5rem;
  gap: 1.125rem;
  grid-auto-flow: column;
  grid-auto-columns: calc(40% - 0.5rem);
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  scroll-padding: 1rem;
  background-color: var(--Yellow);
}
.gallery__image {
  border-radius: 22px;
  scroll-snap-align: start;
  object-fit: contain;
  box-shadow: 0 0 26px var(--Dark-moderate-cyan);
}

.footer {
  align-items: center;
  justify-content: space-around;
  height: 18.75rem;
  padding: 1rem;
  background-color: var(--Dark-moderate-cyan);
}
.footer__h3 {
  font-size: 1.75rem;
  color: var(--Very-dark-desaturated-blue);
}
.footer__bottom {
  gap: 2rem;
  align-items: center;
}
.footer__icons {
  aspect-ratio: 2/2;
  object-fit: cover;
}
.footer__copyright {
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.footer__author {
  font-size: 600;
}

.wrap-reverse {
  flex-wrap: wrap-reverse;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

img,
video,
svg,
iframe {
  display: block;
  max-width: 100%;
  width: 100%;
}

button {
  background: transparent;
  border: 0.5px solid transparent;
  cursor: pointer;
  text-transform: capitalize;
}

a,
:active {
  text-decoration: none;
  color: var(--Dark-blue);
  font-family: "Fraunces", serif;
}

h1,
h2,
h3,
h4 {
  font-family: "Fraunces", serif;
  color: var(--Very-dark-desaturated-blue);
}

p,
span {
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
  line-height: 1.5;
}

:root {
  --Soft-red: hsl(7, 99%, 70%);
  --Yellow: hsl(51, 100%, 49%);
  --Dark-desaturated-cyan: hsl(167, 40%, 24%);
  --Dark-blue: hsl(198, 62%, 26%);
  --Dark-moderate-cyan: hsl(168, 35%, 49%);
}

:root {
  --Very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --Very-dark-grayish-blue: hsl(213, 9%, 39%);
  --Dark-grayish-blue: hsl(232, 10%, 55%);
  --Grayish-blue: hsl(210, 4%, 67%);
  --White: hsl(0, 0%, 100%);
}

.show {
  display: flex;
  transition: all 0.5s ease-in-out;
  transform: translateX(0);
}

.close-btn {
  background-image: url(../../../assets/icons/icon-arrow-down.svg);
}

.fixed-nav {
  display: flex;
  position: fixed;
  inset: 0 0 auto 0;
  z-index: 100;
  backdrop-filter: blur(1em);
  background: linear-gradient(var(--Dark-moderate-cyan), rgba(224, 218, 218, 0));
  color: var(--White);
}

.music-video {
  position: absolute;
  inset: 20px;
  display: none;
  z-index: 55;
}

.show-me-video {
  display: block;
}

.about__music--video {
  aspect-ratio: 4/4;
  object-fit: cover;
}

.preloader {
  position: fixed;
  inset: 0;
  background-color: var(--White);
  display: grid;
  place-items: center;
  grid-template-areas: "loader";
  visibility: visible;
  z-index: 999;
  transition: all 2s ease-out;
}
.preloader__animation {
  grid-area: loader;
  z-index: 5;
  font-size: 4rem;
  color: var(--White);
  font-family: "Fraunces", serif;
}
.preloader__image {
  object-fit: contain;
  object-position: bottom;
  grid-area: loader;
}
@media (min-width: 40em) {
  .preloader__image {
    aspect-ratio: 0;
  }
}

.hide-preloader {
  z-index: -999;
  visibility: hidden;
}